$(document).ready(function () {

	var popup = new Popup();
	popup.constructor();

	$('body').on('click', '.linkToSalesforce', function (e) {
		e.preventDefault();
		var data = window.salesforceClientSampleData;

		chrome.tabs.query({
			active: true,
			currentWindow: true
		}, function (tabs) {
			chrome.tabs.sendMessage(tabs[0].id, { from: 'popup', resource_url: data.resource_url });
		});
	});
});
